.loader {
  width: 50px;
  height: 50px;
  border-radius: 50%;
  border: 3px teal dashed;
  animation: rotate 2s infinite linear;
}

@keyframes rotate {
  from {
    transform: rotate(0deg) scale(1);
  }
  50%{
    transform: rotate(180deg) scale(1.4);
  }
  to {
    transform: rotate(360deg) scale(1);
  }
}
